<template>
    <div class="Home">

        <swipe-up v-if="touchHelper" />


<!--        Desktop with touch      -->
        <transition name="confirm__tr">
            <div class="confirm" v-if="confirmcp">
                <div class="questions">
                    <span>دستگاه شما از تاچ اسکرین پشتیبانی میکند آیا تمایل به هنگام سازی سایت با این قابلیت دارید؟</span>
                </div>
                <div class="answers">
                    <span class="false-result" @click="tochView(false)">خیر</span>
                    <span class="true-result" @click="tochView(true)">بله</span>
                </div>
            </div>
        </transition>

<!--        mobile fullScreen      -->
        <transition name="confirm__tr" appear>
            <div class="confirm" v-if="confirmFS">
                <div class="questions">
                    <span>سایت در حالت تمام صفحه عملکرد بهتری دارد آیا تمایل به تمام صفحه شدن دارید؟</span>
                    <br>
                    <span style="font-size: .8rem">در هر زمان با 2 بار ضربه زدن بر روی صفحه میتوانید از حالت تمام صفحه خارج و یا به آن وارد شوید</span>
                    <br><br>
                </div>
                <div class="answers">
                    <span class="false-result" @click="FullScreenView(false)">خیر</span>
                    <span class="true-result" @click="FullScreenView(true)">بله</span>
                </div>
            </div>
        </transition>


<!--        Iphone view -->
        <transition name="confirm__tr" appear>
            <div class="confirm" v-if="confirmIOS">
                <div class="questions" dir="rtl">
                    <span>اگر دارید از دستگاه Iphone/ios سایت را مرور میکنید بهتر است وبسایت را به صفحه Home خود اضافه کنید.</span>
                    <br>
                    <span style="font-size: .8rem">در این حالت سایت عمل کرد بهتری دارد</span>
                    <br>
                    <span style="font-size: .8rem"><a style="color: #3498db; cursor: pointer ; text-decoration: none" target="_blank" href="https://digiato.com/article/2019/02/28/%D9%85%D8%B3%D8%AF%D9%88%D8%AF-%D8%B4%D8%AF%D9%86-%D8%A7%D8%B3%D9%86%D9%BE-%D8%A7%D8%B6%D8%A7%D9%81%D9%87-%DA%A9%D8%B1%D8%AF%D9%86-%D8%A2%DB%8C%DA%A9%D9%86-%D8%AA%D8%AD%D8%AA-%D9%88%D8%A8">آموزش</a></span>
                    <br><br>
                </div>
                <div class="answers">
                    <span class="false-result" @click="iphoneDone">باشه</span>
                </div>
            </div>
        </transition>



            <transition
                    :enter-active-class="enter"
                    :leave-active-class="leave"
                    mode="out-in"
                    appear
            >
                <component class="tmp" :is="whatIs"></component>
            </transition>

        <up-arrow v-if="desktopView" />
        <down-arrow v-if="desktopView" />
    </div>
</template>

<script>

    import wlcView from '@/components/Main/Wlc.vue'
    import aboutUs from '@/components/Main/About.vue'
    import aboutUs2 from '@/components/Main/About2.vue'
    import whatweDo from '@/components/Main/WhatWeDo.vue'
    import projectExample from '@/components/Main/projectExample.vue'
    import allDone from '@/components/Main/allDone.vue'
    import contactUs from '@/components/Main/contactUs.vue'
    import downArrow from '@/components/arrows/DownArrow.vue'
    import upArrow from '@/components/arrows/UpArrow.vue'
    import swipeUp from '@/components/TouchHelper/SwipeUpHelper.vue'



    export default {
        data(){
            return{
                startX : 0 ,
                startY : 0 ,
                mobileDevice : false,
                touchable : false,
                closeListener : false,
                confirm : false,
                fullscreen : false,
                desktopV : true,
                touchAppear : false,
                iphone : false,
                tmp : '' ,

            }
        },
        components:{
            wlcView : wlcView,
            aboutUs : aboutUs,
            aboutUs2 : aboutUs2,
            whatweDo :whatweDo,
            projectExample :projectExample,
            allDone: allDone,
            contactUs : contactUs,
            downArrow,
            upArrow,
            swipeUp,


        },
        beforeCreate() {

        },
        mounted() {
            if(this.mobileDevice == false && this.touchable == true){
                this.confirm = true
            }
        },
        created() {

            if(/Android|webOS|Macintosh|iPhone|iPad|iPod|BlackBerry|KFAUWI|Nokia|PlayBook|BB|KFAPWI|IEMobile|Opera Mini/i.test(navigator.userAgent)){
                this.mobileDevice = true
                if(/iPhone|iPad|iPod|Macintosh/i.test(navigator.userAgent)){
                    if(navigator.maxTouchPoints>3){
                        this.iphone = true
                    }else{
                        this.mobileDevice = false
                    }
                }else{
                    if(document.fullscreenElement){
                        this.fullscreen = false
                    }else{
                        this.fullscreen = true
                    }
                }
            }else{
                this.mobileDevice = false
            }
            if ("ontouchstart" in document.documentElement){
                this.touchable = true
            }else{
                this.touchable = false
            }
            if(this.mobileDevice){
                window.addEventListener("touchstart", this.touchStart);
                window.addEventListener("touchmove", this.touchMove);
                this.closeListener = true;
                this.desktopV = false;
            }
        },
        unmounted() {
            if(this.closeListener){
                window.removeEventListener("touchstart", this.touchStart);
                window.removeEventListener("touchmove", this.touchMove);
            }
        },
        methods:{
            iphoneDone(){
                this.touchAppear = true;
                this.iphone = false  ;
            },
            FullScreenView(Ans){
                if(Ans){
                    this.fullscreen = false //confirm box close
                    this.toggleFullScreen()
                    setTimeout(()=>{
                        this.touchAppear = true
                    },300)
                }else{
                    this.fullscreen = false
                    setTimeout(()=>{
                        this.touchAppear = true
                    },300)
                }
            },
            tochView(Ans){
                if(Ans){
                    this.desktopV = false;
                    this.confirm = false
                    this.closeListener = true
                    this.touchAppear = true
                    window.addEventListener("touchstart", this.touchStart);
                    window.addEventListener("touchmove", this.touchMove);
                }else{
                    this.confirm = false
                    this.closeListener = false
                }
            },
            toggleFullScreen(){
                let isInFullScreen = (document.fullscreenElement && document.fullscreenElement !== null) ||
                    (document.webkitFullscreenElement && document.webkitFullscreenElement !== null) ||
                    (document.mozFullScreenElement && document.mozFullScreenElement !== null) ||
                    (document.msFullscreenElement && document.msFullscreenElement !== null);
                let element = document.documentElement;
                if (!isInFullScreen) {
                    if (element.requestFullscreen) {
                        element.requestFullscreen();
                    } else if (element.mozRequestFullScreen) {
                        element.mozRequestFullScreen();
                    } else if (element.webkitRequestFullScreen) {
                        element.webkitRequestFullScreen();
                    } else if (element.msRequestFullscreen) {
                        element.msRequestFullscreen();
                    }
                } else {
                    if (document.exitFullscreen) {
                        document.exitFullscreen();
                    } else if (document.webkitExitFullscreen) {
                        document.webkitExitFullscreen();
                    } else if (document.mozCancelFullScreen) {
                        document.mozCancelFullScreen();
                    } else if (document.msExitFullscreen) {
                        document.msExitFullscreen();
                    }
                }
            },
            touchStart(e){
                // window.removeEventListener("touchstart", this.touchStart);
                this.startX = e.touches[0].clientX
                this.startY = e.touches[0].clientY
                this.YS = e.touches[0].clientY
            },
            touchMove(e){
                if(this.confirmFS == false && this.confirmIOS == false ){
                    let tchEndX = e.touches[0].clientX;
                    let tchEndY = e.touches[0].clientY;
                    this.YE = e.touches[0].clientY
                    let touchResultX = tchEndX - this.startX ;
                    let touchResultY = tchEndY - this.startY ;
                    if(touchResultY <= -10 && Math.abs(touchResultX)<5){
                        this.touchAppear = false
                        window.removeEventListener("touchmove", this.touchMove);
                        let index = this.$store.state.current[1];
                        if(index<5){
                            this.$store.state.animationMethods[0]= 'leave-to-up'
                            this.$store.state.animationMethods[1]= 'enter-from-down'
                            this.$store.state.current[0] = this.$store.state.pages[index+1].name
                            this.$store.state.current[1] = this.$store.state.pages[index+1].index
                        }
                    }else if(touchResultY  >= 10 && Math.abs(touchResultX)<5){
                        window.removeEventListener("touchmove", this.touchMove);
                        let index = this.$store.state.current[1];
                        if(index>0){
                            this.$store.state.animationMethods[0]= 'leave-to-down'
                            this.$store.state.animationMethods[1]= 'enter-from-up'
                            this.$store.state.current[0] = this.$store.state.pages[index-1].name
                            this.$store.state.current[1] = this.$store.state.pages[index-1].index
                        }
                    }
                    setTimeout(()=>{
                        window.addEventListener("touchmove", this.touchMove);
                    },500)
                }
            },
        },
        computed:{
            whatIs(){
                return this.$store.state.current[0];
            },
            leave(){
                return this.$store.state.animationMethods[0];
            },
            enter(){
                return this.$store.state.animationMethods[1];
            },
            confirmcp(){
                return this.confirm
            },
            confirmFS(){
                return this.fullscreen
            },
            confirmIOS(){
              return this.iphone
            },
            desktopView(){
                return this.desktopV
            },
            touchHelper(){
                return this.touchAppear
            }
            
        }
    }
</script>

<style scoped>

    .Home{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: relative;
        overflow: hidden;
        background-color: #202124;
    }
    .bg__holder{
        width: 100%;
        height: 100%;
        position: fixed;
        background-image: url("../assets/digi_Tris.gif");
        background-repeat: no-repeat;
        background-position: center;
        z-index: -10000;
        background-size: 100vw 100vh;
    }
    .bf--mob{
        background-size: 150vw 100vh !important;
    }
    .confirm{
        position: fixed;
        top: 0px;
        left: 50%;
        transform: translateX(-50%);
        width: 80%;
        min-height: 20%;
        height: auto;
        background-color: #3c4042;
        color: #e5b5d4;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        text-align: center;
        padding: 10px 5px 10px 5px;
        box-sizing: border-box;
        border-radius: 10px;
        font-size: 1rem;
        z-index: 100000000;
    }
    .answers{
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }

    .answers span{
        background-color: #c489bc;
        padding: 3px;
        border-radius: 5px;
        width: 4rem;
        color: #1a241c;
        cursor: pointer;
        transition: .5s ease;
    }
    .answers span:hover{
        background-color: #9f58a3;
        color: #fff;
    }

    .confirm__tr-enter-active{
       animation: slideIn-top .5s ease-out;
    }
    .confirm__tr-leave-active{
        animation: slideIn-top .5s ease-out reverse;
    }

    @keyframes slideIn-top {
        0%{
            top: -100%;
        }
        100%{
           top: 0;
        }
    }

    .enter-from-up{
        animation: swip1 .5s ease reverse;
    }
    .leave-to-up{
        animation: swip1 .5s ease;
    }
    .enter-from-down{
        animation: swip2 .5s ease;
    }
    .leave-to-down{
        animation: swip2 .5s ease reverse;
    }



    @keyframes swip1 {
        0%{
            transform: translateY(0);
        }
        100%{
            transform: translateY(-100%);
        }
    }
    @keyframes swip2 {
        0%{
            transform: translateY(100%);
        }
        100%{
            transform: translateY(0);
        }
    }

</style>

