<style scoped>

    .swipeUp{
        position: absolute;
        top:80%;
        width: 100px;
        height: 100px;
        z-index: 10000;
        cursor: pointer;
        animation: finger 2.5s ease-in-out infinite;
        -webkit-animation: finger 2.5s ease-in-out infinite;
    }
    .swiperUp {
        fill: #868686;
        width: 100%;
        height: 100%;
    }

    .apperTouchHelperr-enter-active{
        animation: Touchappear .5s ease-in-out;
    }
    .apperTouchHelper-leave-active{
        animation: Touchappear .5s ease-in-out reverse;
    }
    @keyframes Touchappear {
        from{
            opacity: 0;
        }
        to{
            opacity: 1;
        }
    }
    .touched{
        animation: touches 2.5s ease-in-out infinite;
    }
    @keyframes touches {
        0%{
            opacity: 0;
        }
        10%{
            opacity: 1;
        }
        100%{
            opacity: 0;
        }
    }

    @keyframes finger {
        0%{
            transform: scale(1.3);
        }
        10%{
            transform: scale(1);
        }
        70%{
            transform: scale(1);
        }
        80%{
            top: 60%;
        }
        100%{
            transform: scale(1.3);
        }
    }

    @media only screen and (max-height:600px) {

        .swipeUp{
            top:70%;
        }
        @keyframes finger {
            0%{
                transform: scale(1.3);
            }
            10%{
                transform: scale(1);
            }
            70%{
                transform: scale(1);
            }
            80%{
                top: 40%;
            }
            100%{
                transform: scale(1.3);
            }
        }
    }

</style>