<template>
    <div class="__cnt container">
        <span class="campidea">
            <span class="camp__iner">C</span>
            <span class="camp__iner">a</span>
            <span class="camp__iner">m</span>
            <span class="camp__iner">p</span>
            <span class="camp__iner">I</span>
            <span class="camp__iner">d</span>
            <span class="camp__iner">e</span>
            <span class="camp__iner">a</span>
        </span>

        <span class="slogan">جایی برای پرورش ایده های شما</span>
    </div>
</template>

<script>
    export default {
        mounted() {
            let cnt = document.getElementsByClassName("__cnt");

            for (let i = 0 ; i < cnt.length; i++) {
                cnt[i].addEventListener('dblclick',this.toggleFullScreen);
            }
        },
        methods:{
            toggleFullScreen(){
                let isInFullScreen = (document.fullscreenElement && document.fullscreenElement !== null) ||
                    (document.webkitFullscreenElement && document.webkitFullscreenElement !== null) ||
                    (document.mozFullScreenElement && document.mozFullScreenElement !== null) ||
                    (document.msFullscreenElement && document.msFullscreenElement !== null);
                let element = document.documentElement;
                if (!isInFullScreen) {
                    if (element.requestFullscreen) {
                        element.requestFullscreen();
                    } else if (element.mozRequestFullScreen) {
                        element.mozRequestFullScreen();
                    } else if (element.webkitRequestFullScreen) {
                        element.webkitRequestFullScreen();
                    } else if (element.msRequestFullscreen) {
                        element.msRequestFullscreen();
                    }
                } else {
                    if (document.exitFullscreen) {
                        document.exitFullscreen();
                    } else if (document.webkitExitFullscreen) {
                        document.webkitExitFullscreen();
                    } else if (document.mozCancelFullScreen) {
                        document.mozCancelFullScreen();
                    } else if (document.msExitFullscreen) {
                        document.msExitFullscreen();
                    }
                }
            },
        },
        unmounted() {
            let cnt = document.getElementsByClassName("__cnt");
            for (let i = 0 ; i < cnt.length; i++) {
                cnt[i].removeEventListener('dblclick',this.toggleFullScreen);
            }
        },
    }
</script>

<style scoped>

    .container{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        color: #606369;
        background-color: #202124;
    }

    .campidea{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .camp__iner{
        opacity: 0;
        font-size: 6rem;
        font-family: 'Yellowtail' ;
    }
    .camp__iner:nth-child(1){
        animation: showUp .7s ease forwards;
        animation-delay: .3s;
    }
    .camp__iner:nth-child(2){
        animation: showUp .7s ease forwards;
        animation-delay: .4s;
    }
    .camp__iner:nth-child(3){
        animation: showUp .7s ease forwards;
        animation-delay: .5s;
    }
    .camp__iner:nth-child(4){
        animation: showUp .7s ease forwards;
        animation-delay: .6s;
    }
    .camp__iner:nth-child(5){
        animation: showUp .7s ease forwards;
        animation-delay: .7s;
    }
    .camp__iner:nth-child(6){
        animation: showUp .7s ease forwards;
        animation-delay: .8s;
    }
    .camp__iner:nth-child(7){
        animation: showUp .7s ease forwards;
        animation-delay: .9s;
    }
    .camp__iner:nth-child(8){
        animation: showUp .7s ease forwards;
        animation-delay: 1s;
    }


    @keyframes showUp {
        0%{
            opacity: 0;
        }
        100%{
            opacity: 1;
        }
    }

    .slogan{
        animation: .5s fadeUp ease forwards;
        opacity: 0;
        animation-delay: 1.2s;
        font-size: 1.2rem;
    }

    @keyframes fadeUp {
        0%{
            opacity: 0;
            transform: translateY(50%);
        }
        100%{
            opacity: 1;
            transform: translateY(0);
        }
    }
</style>