<template>
    <transition name="arrowShows" mode="out-in" appear>
        <div class="arrow__container Adown" @click="goDown" v-if="showUp < maxLength-1 && shows">
            <svg data-name="Layer 2" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 420 664">
                <polygon class="__ar" points="0 542 210 664 420 544 420 396 210 513 0 396 0 542"/>
                <polygon class="__ar" points="0 349 210 471 420 351 420 203 210 320 0 203 0 349"/>
                <polygon class="__ar" points="0 146 210 268 420 148 420 0 210 117 0 0 0 146"/>
            </svg>
        </div>
    </transition>
</template>

<script>
    export default {
        data(){
            return{
                maxLength : this.$store.state.pages.length,
                shows : true,
            }
        },
        methods:{
            goDown(){
                this.shows = false
                let index = this.$store.state.current[1];
                if(index<5){
                    this.$store.state.animationMethods[0]= 'leave-to-up'
                    this.$store.state.animationMethods[1]= 'enter-from-down'
                    this.$store.state.current[0] = this.$store.state.pages[index+1].name
                    this.$store.state.current[1] = this.$store.state.pages[index+1].index
                }
                setTimeout(()=>{
                    this.shows = true;
                },300)
            },

        },
        computed: {
            showUp(){
                return this.$store.state.current[1];
            }
        }
    }
</script>

<style scoped>

    .arrow__container{
        position: fixed;
        width: auto;
        height: 10%;
        display: flex;
        justify-content: center;
        align-items: center;

    }
    .arrow__container svg{
        width: auto;
        height: 100%;
    }
    .Adown{
        cursor: pointer;
        left: 50%;
        bottom: 10px;
        transform: translateX(-50%);
    }
    .__ar{
        fill: #606368;
    }
    .__ar:nth-child(3){
        animation: flashing 1s linear infinite ;
        animation-delay: .3s;
    }
    .__ar:nth-child(2){
        animation: flashing 1s linear infinite ;
        animation-delay: .6s;
    }
    .__ar:nth-child(1){
        animation: flashing 1s linear infinite ;
        animation-delay: .9s;
    }
    @keyframes flashing {
        0%{
            fill: #606368;
            opacity: 0;
        }
        100%{
            fill: #ffb2ff;
            opacity: 1;
        }
    }
    .arrowShows-enter-active{
        animation: shows .3s ease-in-out;
    }
    .arrowShows-leave-active{
        animation: shows .3s ease reverse;
    }
    @keyframes shows {
        0%{
            opacity: 0;
        }
        100%{
            opacity: 1;
        }
    }
</style>