<template>
    <div class="__cnt container">
        <div class="whatwedo-container">
            <div class="title-holder">
                <h1 style="margin-left: 10px">CampIdea</h1>
                <span>چیست؟</span>
            </div>
            <div class="wwdo-itself" dir="rtl">
                <span>
                    درواقع CampIdea یا به اختصار CI615 یک مجموعه است بسیار مشابه با استارت اپ های داخل مارکت،
                با این تفاوت که هدف اصلی این گروه سرمایه گذاری و پیشبرد ایده های Unique میباشد.
                     ایده ی CI سال 1392 شکل گرفت
                    و از آن تاریخ تا به امروز با بزرگتر شدن اهدافش خودش نیز بزرگ تر شده است.
                    به لطف حمایت ها و زحمات اعضای این گروه حالا ما آماده ایم تا وارد پروژه های بزرگ تر  و هدف مند تری شویم
                    اعضای اصلی این گروه شامل 12 نفر میباشد که هرکدام  در زمینه تخصصی خود سال ها زحمت کشیده اند و همیشه سعی در به ثمر رساندن یک نتیجه ی حرفه ای داشته اند.
                </span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        mounted() {
            let cnt = document.getElementsByClassName("__cnt");

            for (let i = 0 ; i < cnt.length; i++) {
                cnt[i].addEventListener('dblclick',this.toggleFullScreen);
            }
        },
        methods:{
            toggleFullScreen(){
                let isInFullScreen = (document.fullscreenElement && document.fullscreenElement !== null) ||
                    (document.webkitFullscreenElement && document.webkitFullscreenElement !== null) ||
                    (document.mozFullScreenElement && document.mozFullScreenElement !== null) ||
                    (document.msFullscreenElement && document.msFullscreenElement !== null);
                let element = document.documentElement;
                if (!isInFullScreen) {
                    if (element.requestFullscreen) {
                        element.requestFullscreen();
                    } else if (element.mozRequestFullScreen) {
                        element.mozRequestFullScreen();
                    } else if (element.webkitRequestFullScreen) {
                        element.webkitRequestFullScreen();
                    } else if (element.msRequestFullscreen) {
                        element.msRequestFullscreen();
                    }
                } else {
                    if (document.exitFullscreen) {
                        document.exitFullscreen();
                    } else if (document.webkitExitFullscreen) {
                        document.webkitExitFullscreen();
                    } else if (document.mozCancelFullScreen) {
                        document.mozCancelFullScreen();
                    } else if (document.msExitFullscreen) {
                        document.msExitFullscreen();
                    }
                }
            },
        },
        unmounted() {
            let cnt = document.getElementsByClassName("__cnt");
            for (let i = 0 ; i < cnt.length; i++) {
                cnt[i].removeEventListener('dblclick',this.toggleFullScreen);
            }
        },
    }
</script>

<style scoped>
    .container{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        color: #606369;
        background-color: #202124;
    }
    .whatwedo-container{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: column;
        width: 85%;
        height: 77%;
    }
    .title-holder{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row-reverse;

    }
    .wwdo-itself{
        height: auto;
        width: 50%;
        padding: 5px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    @media only screen and (max-width: 700px) {

        .wwdo-itself{
            width: 100%;
        }
        .whatwedo-container{
            width: 85%;
            height: 100%;
        }
    }
    @media only screen and (max-height:600px) {
        .wwdo-itself{
            height: 85%;
        }
    }
</style>