<template>
    <div class="__cnt container">
            <div class="about-container">
                <div class="imageHolder" :style="{border : hexColor ,boxShadow : boxShadow}">
                    <img class="signature" src="../../assets/p4Sign.png" alt="">
                </div>

                <div class="about-itself" dir="rtl">
                    <span>
                    تجربه 8 ساله‌ی برنامه نویسی من از سن خیلی کم، با علاقهمند شدن به کامپیوتر شروع شد و با ++C برنامه نویسی را آغاز کردم.
                    خیلی طولی نکشید که متوجه شدم ++C برای شروع انتخاب خوبی نبوده!(سلیقه ای)
                    برای همین وارد دنیای python شدم و اولین برنامه‌ی خودم را که یک برنامه‌ی حسابداری تحت ویندوز بود نوشتم.
                    سپس وارد دنیای وب شدم و الان حدود 3 سالی میشه که دارم برنامه نویسی تحت وب انجام میدم.
                    </span>
                </div>
                <div class="footer-about">
                    <a href="https://developer.mozilla.org/en-US/docs/Web/JavaScript" target="_blank"><img src="../../assets/js.png" alt="javascript" class="img-footer"></a>
                    <a href="https://vuejs.org/" target="_blank"><img src="../../assets/Vue.png" alt="vue" class="img-footer"></a>
                    <a href="https://www.php.net/" target="_blank"><img src="../../assets/php.png" alt="php" class="img-footer"></a>
                    <a href="https://laravel.com/" target="_blank"><img src="../../assets/laravel.png" alt="laravel" class="img-footer"></a>
                    <a href="https://www.python.org/" target="_blank"><img src="../../assets/py.png" alt="python" class="img-footer"></a>
                    <a href="https://www.djangoproject.com/" target="_blank"><img src="../../assets/django.png" alt="django" class="img-footer"></a>
                    <a href="https://g.co/kgs/grdJ3m" target="_blank"><img src="../../assets/UiUx.png" alt="ui & ux design" class="img-footer"></a>
                </div>
            </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                HexColor1 : 'ff',
                HexColor2 : 'ff',
                HexColor3 : 'ff',
            }
        },
        mounted() {
            let cnt = document.getElementsByClassName("__cnt");

            for (let i = 0 ; i < cnt.length; i++) {
                cnt[i].addEventListener('dblclick',this.toggleFullScreen);
            }

            setInterval(()=>{
                this.HexColor1 = Math.floor((Math.random() * 100) + 10)
                this.HexColor2 = Math.floor((Math.random() * 100) + 10)
                this.HexColor3 = Math.floor((Math.random() * 100) + 10)
            },1000)
        },
        methods:{
            toggleFullScreen(){
                let isInFullScreen = (document.fullscreenElement && document.fullscreenElement !== null) ||
                    (document.webkitFullscreenElement && document.webkitFullscreenElement !== null) ||
                    (document.mozFullScreenElement && document.mozFullScreenElement !== null) ||
                    (document.msFullscreenElement && document.msFullscreenElement !== null);
                let element = document.documentElement;
                if (!isInFullScreen) {
                    if (element.requestFullscreen) {
                        element.requestFullscreen();
                    } else if (element.mozRequestFullScreen) {
                        element.mozRequestFullScreen();
                    } else if (element.webkitRequestFullScreen) {
                        element.webkitRequestFullScreen();
                    } else if (element.msRequestFullscreen) {
                        element.msRequestFullscreen();
                    }
                } else {
                    if (document.exitFullscreen) {
                        document.exitFullscreen();
                    } else if (document.webkitExitFullscreen) {
                        document.webkitExitFullscreen();
                    } else if (document.mozCancelFullScreen) {
                        document.mozCancelFullScreen();
                    } else if (document.msExitFullscreen) {
                        document.msExitFullscreen();
                    }
                }
            },
        },
        unmounted() {
            let cnt = document.getElementsByClassName("__cnt");
            for (let i = 0 ; i < cnt.length; i++) {
                cnt[i].removeEventListener('dblclick',this.toggleFullScreen);
            }
        },
        computed : {
            hexColor(){
                return '3px solid #'+this.HexColor1+''+this.HexColor2+''+this.HexColor3
            },
            boxShadow(){
                return '1px 1px 41px 1px #'+this.HexColor1+''+this.HexColor2+''+this.HexColor3
            }
        }
    }
</script>

<style scoped>
    .container{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        color: #606369;
        background-color: #202124;
    }
    .about-container{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: column;
        width: 85%;
        height: 77%;
    }
    .imageHolder{
        min-width: 9.5rem;
        min-height: 9.5rem;
        max-width: 9.5rem;
        max-height: 9.5rem;
        border-radius: 50%;
        background-image: url("../../assets/logo-phm.png");
        background-size: cover;
        /*background-position: center center;*/
        transition: .5s ease-in-out;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .signature{
        position: absolute;
        bottom: -110px;
        width: 100%;
    }
    .about-itself{
        height: auto;
        width: 50%;
        padding: 5px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .footer-about{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        height: auto;
        width: 50%;
        padding: 5px;
    }
    .img-footer{
        width: 50px;
        height: 50px;
        opacity: 1;
    }
    @media only screen and (max-width: 700px) {
      .footer-about{
          width: 100%;
      }
        .img-footer{
            width: 30px;
            height: 30px;
        }
        .about-itself{
            width: 100%;
        }
        .about-container{
            width: 85%;
            height: 100%;
        }
    }
    @media only screen and (max-height:600px) {
        .about-container{
            height: 85%;
        }
    }
</style>